import React, {Component} from 'react';
import { Slide } from 'react-slideshow-image';


class Slideshow extends Component {
    render(){
        const slideImages = [
            `${this.props.image1}`,
            `${this.props.image2}`,
            `${this.props.image3}`
        ];
        
        const properties = {
            duration: 6500,
            transitionDuration: 500,
            infinite: true,
            indicators: true,
            arrows: true
        }
    return(
        <Slide { ...properties }>
            <div className="each-slide">
                <div style={{ 'backgroundImage': `url(${slideImages[0]})`, 'background-size': 'cover', 'height': '400px', 'width': '100%' }}>

                </div>
            </div>
            <div className="each-slide">
                <div style={{ 'backgroundImage': `url(${slideImages[1]})`, 'background-size': 'cover', 'height': '400px', 'width': '800px' }}>

                </div>
            </div>
            <div className="each-slide">
                <div style={{ 'backgroundImage': `url(${slideImages[2]})`, 'background-size': 'cover', 'height': '400px', 'width': '800px' }}>

                </div>
            </div>
        </Slide>
    
    )}
}

 export default Slideshow;


